/* Main Container */
.tournament-container {
  padding: 60px 20px;
  background: linear-gradient(135deg, #00796b, #004d40);
  font-family: 'Poppins', sans-serif;
  color: white;
  text-align: center;
  min-height: 100vh;
}

/* Header Section */
.tournament-header {
  margin-bottom: 40px;
}

.tournament-header h1 {
  font-size: 3rem;
  color: #ffffff;
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 700;
}

.tournament-header p {
  font-size: 1.2rem;
  color: #c8e6c9;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.8;
}

/* Tournament Cards */
.tournament-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  padding: 20px;
  justify-items: center;
}

.tournament-card {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 30px;
  text-align: left;
  color: #ffffff;
  position: relative;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  z-index: 1;
}

.tournament-card h2 {
  font-size: 2rem;
  color: #ffffff;
  margin-bottom: 10px;
}

.tournament-card p {
  font-size: 1.1rem;
  color: #c8e6c9;
  line-height: 1.6;
}

.tournament-card .cta {
  display: inline-block;
  background-color: #e0f7fa;
  color: #004d40;
  padding: 12px 30px;
  margin-top: 20px;
  border-radius: 30px;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.tournament-card .cta:hover {
  background-color: #b2dfdb;
  transform: translateY(-5px);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
}


.tournament-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3);
}


.tournament-container::before,
.tournament-container::after {
  content: '';
  position: absolute;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  z-index: 0;
}

.tournament-container::before {
  width: 200px;
  height: 200px;
  top: 20px;
  left: 10%;
}

.tournament-container::after {
  width: 300px;
  height: 300px;
  bottom: 50px;
  right: 10%;
}


@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


@media (max-width: 768px) {
  .tournament-header h1 {
    font-size: 2.2rem;
  }

  .tournament-header p {
    font-size: 1rem;
  }

  .tournament-card {
    padding: 20px;
  }

  .tournament-card h2 {
    font-size: 1.6rem;
  }

  .tournament-card p {
    font-size: 1rem;
  }

  .tournament-cards {
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .tournament-header h1 {
    font-size: 1.8rem;
  }

  .tournament-header p {
    font-size: 0.9rem;
  }

  .tournament-card h2 {
    font-size: 1.4rem;
  }

  .tournament-card p {
    font-size: 0.9rem;
  }
}
