
.home-container {
  font-family: "Roboto", sans-serif;
    color: black;
    background-color: #006254;
    text-align: center;
    font-family: "Roboto", sans-serif;
    height: 100vh;
    
  }
  
  
  .hero-section {
    background-image: url('../assets/Explainer\ Video_\ CricketID.webp'); 
    background-size: cover;
    background-position: center;
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    position: relative;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.3);
    font-family: "Roboto", sans-serif;
  }
  
  .hero-content h1 {
    font-size: 48px;
    margin-bottom: 20px;
    padding: 10px;
    text-shadow: 2px 2px 5px rgb(255, 255, 255);
    font-family: "Bungee Tint", sans-serif;
    border: white;
    border-radius: 20px;
  }
  
  .hero-content p {
    font-size: 24px;
    margin-bottom: 30px;
    font-family: "Roboto", sans-serif;
    color: #fbff00;
    font-weight: 800;
    text-shadow: 2px 2px 3px rgba(10, 10, 10, 0.555);
  }
  
  .cta-button {
    display: inline-block;
  background-color: #ffef78;
  color: #503d3d;
  padding: 12px 30px;
  margin-top: 20px;
  border-radius: 30px;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  .cta-button:hover{
  background-color: #cbce1b;
  transform: translateY(-5px);
  color: #503d3d;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
  }
  .cta-lol{
    display: inline-block;
  background-color: #ffef78;
  color: #503d3d;
  padding: 12px 30px;
  margin-top: 20px;
  border-radius: 30px;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  .cta-lol:hover{
    background-color: #cbce1b;
  transform: translateY(-5px);
  color: #503d3d;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
  }
  
 
  .features-section {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 15px 0;
  }
  
  .feature-card {
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    width: 360px;
    height: 200px;
    margin: 5px;
    border-radius: 15px;
    padding: 30px;
    text-align: center;
    color: #ffffff;
    transition: transform 0.3s;
    font-family: "Roboto", sans-serif;
  }
  
  .feature-card h2 {
    font-size: 22px;
    margin-bottom: 15px;
    color: aliceblue;
    font-family: "Bungee Tint", sans-serif;
    text-shadow: 2px 2px 3px rgba(255, 255, 255, 0.632);
    
  }
  
  .feature-card p {
    font-size: 15px;
    margin-bottom: 15px;
    color: #c8e6c9;
  }
  
 
  .feature-card:hover {
    transform: scale(1.05);
  }
  
  
  
  @media (max-width: 768px) {
    .hero-content h1 {
      font-size: 32px;
    }
    .home-container{
      height: 170vh;
    }
  
    .hero-content p {
      font-size: 10px;
    }
  
    .features-section {
      flex-direction: column;
      align-items: center;
    }
  
    .feature-card {
      width: 90%;
      padding: 0%;
    }
    .feature-card h2 {
      font-size: 18px;
    }
    .feature-card p {
      font-size: 13px;
    }
    .cta-lol{
      padding: 9px;
      font-size: 12px;
    }
  }
  