
.about-container {
    padding: 50px 20px;
    background: linear-gradient(135deg, #004d40, #00695c);
    color: white;
    font-family: 'Poppins', sans-serif;
    line-height: 1.7;
    text-align: center;
    animation: fadeIn 1s ease-in-out;
  }
  
  
  .about-header {
    margin-bottom: 50px;
  }
  
  .about-header h1 {
    font-size: 3rem;
    color: #ffffff;
    margin-bottom: 15px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 700;
  }
  
  .about-header p {
    font-size: 1.3rem;
    color: #e0f7fa;
    max-width: 700px;
    margin: 0 auto;
    line-height: 1.8;
  }
  
 
  .about-content {
    max-width: 1000px;
    margin: 0 auto;
    text-align: left;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 40px;
    border-radius: 15px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  }
  
 
  .about-content h2 {
    font-size: 2.5rem;
    color: #e0f7fa;
    margin-bottom: 15px;
    text-transform: uppercase;
    font-weight: 600;
    position: relative;
  }
  
  .about-content h2::after {
    content: '';
    width: 100px;
    height: 4px;
    background-color: #ffffff;
    position: absolute;
    left: 0;
    bottom: -10px;
  }
  
  .about-content p {
    font-size: 1.2rem;
    color: #c8e6c9;
    margin-bottom: 30px;
    line-height: 1.8;
  }
  
  
  .about-container::before,
  .about-container::after {
    content: '';
    position: absolute;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.15);
    z-index: 0;
  }
  
  .about-container::before {
    width: 200px;
    height: 200px;
    top: 50px;
    left: 10%;
  }
  
  .about-container::after {
    width: 300px;
    height: 300px;
    bottom: 80px;
    right: 10%;
  }
  
 
  .cta {
    display: inline-block;
    background-color: #e0f7fa;
    color: #004d40;
    padding: 12px 30px;
    margin-top: 20px;
    border-radius: 30px;
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: bold;
    text-transform: uppercase;
    transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
    z-index: 1;
  }
  
  .cta:hover {
    background-color: #b2dfdb;
    transform: translateY(-5px);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  }
  

  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
 
  @media (max-width: 768px) {
    .about-header h1 {
      font-size: 2.2rem;
    }
  
    .about-header p {
      font-size: 1rem;
    }
  
    .about-content {
      padding: 30px;
    }
  
    .about-content h2 {
      font-size: 2rem;
    }
  
    .about-content p {
      font-size: 1rem;
    }
  }
  
  @media (max-width: 480px) {
    .about-header h1 {
        font-size: 1.1rem;
    }
  
    .about-header p {
      font-size: 0.9rem;
    }
  
    .about-content {
      padding: 20px;
    }
  
    .about-content h2 {
      font-size: 1.7rem;
    }
  }
  