
.app-container {
  position: relative;
  width: 100%;
  height: 90vh; 
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #005d50;
  overflow: hidden;
}



.form-container {
  background: linear-gradient(135deg, #00bfa5, #00796b); 
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  max-width: 500px;
  color: white;
  font-family: 'Poppins', sans-serif;
  text-align: center;
  z-index: 1; 
  position: relative;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  text-transform: uppercase;
}

h2 {
  font-size: 1.8rem;
  margin-bottom: 10px;
}


input,
textarea {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 10px;
  border: none;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.2); 
  color: white;
  outline: none;
  transition: background-color 0.3s ease;
}

input:focus,
textarea:focus {
  background: rgba(255, 255, 255, 0.3); 
}


.form-button {
  background-color: #66bb6a; 
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 30px;
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.form-button:hover {
  background-color: #43a047; 
  transform: translateY(-3px);
}

.form-button.submit {
  background-color: #388e3c; 
}

.form-button.submit:hover {
  background-color: #2e7d32; 
}


.form-step {
  animation: fadeIn 0.5s ease-in-out;
}


@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .form-container {
    padding: 30px;
    margin: 20px;
  }
  .app-container{
    padding-bottom: 40px;
    padding-top: 1px;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  input,
  textarea {
    font-size: 0.9rem;
  }

  .form-button {
    font-size: 1rem;
  }
}
